@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300;400;600&display=swap");
:root {
  --desaturated-red: hsl(0, 36%, 70%);
  --soft-red: hsl(0, 93%, 68%);
  --greyish-red: hsl(0, 6%, 24%);
  --gradient1: linear-gradient(135deg, hsl(0, 0%, 100%), to hsl(0, 100%, 98%));
  --gradient2: linear-gradient(135deg, hsl(0, 80%, 86%), to hsl(0, 74%, 74%));
}

.attribution {
  margin-top: 1em;
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: #3e52a3;
}

body {
  font-family: "Josefin Sans", sans-serif;
}

.logo {
  padding: 2em;
}

.hero-mobile {
  width: 100%;
}

.tag-line {
  width: 90%;
  margin: auto;
  font-size: 3rem;
  line-height: 1.1;
  letter-spacing: 0.2em;
  font-weight: 400;
  text-transform: uppercase;
  color: var(--greyish-red);
  text-align: center;
  margin-top: 1em;
  margin-bottom: 0.5em;
}
.tag-line .fw {
  color: var(--desaturated-red);
  font-weight: 300;
}

.description {
  width: 85%;
  margin: auto;
  text-align: center;
  color: var(--desaturated-red);
}

.email-field {
  width: 85%;
  margin: auto;
  display: flex;
  grid-auto-flow: column;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
  background: none;
  margin-top: 2em;
}
.email-field .email {
  width: 100%;
  border: none;
  outline: none;
  border: 1px solid var(--desaturated-red);
  border-radius: 100px;
  padding: 1em;
}
.email-field .email::placeholder {
  color: var(--desaturated-red);
}
.email-field .error {
  margin-left: -8em;
  width: 2em;
  height: 2em;
  opacity: 0;
}
.email-field .submit {
  background-image: linear-gradient(135deg, var(--desaturated-red), var(--soft-red));
  border: none;
  padding: 1em;
  padding-inline: 2em;
  border-radius: 100px;
  text-align: center;
}
.email-field .submit:hover {
  box-shadow: -8px 9px 24px 0px rgba(0, 0, 0, 0.27);
}

.error-message {
  width: 85%;
  margin: auto;
  padding-inline-start: 1.2em;
  margin-block-start: 0.4em;
  margin-block-end: 1em;
  color: var(--soft-red);
  opacity: 0;
}

.hero-desktop {
  display: none;
}

@media (min-width: 900px) {
  .hero {
    display: grid;
    grid-template-columns: 3fr 2fr;
    height: 100vh;
    align-items: stretch;
  }

  .left-hero {
    background-image: url(../../images/bg-pattern-desktop.svg);
    padding: 2em 6em;
    padding-inline-end: 15em;
  }
  .left-hero .tag-line {
    margin-top: 2em;
    text-align: left;
  }
  .left-hero .description {
    text-align: start;
  }
  .left-hero .email-field {
    margin-top: 3em;
  }

  .right-hero {
    display: grid;
    grid-auto-flow: column;
    align-items: stretch;
  }

  .hero-mobile {
    display: none;
  }

  .hero-desktop {
    display: block;
  }
}